<template>
  <div>
    <div class="no-cart" v-if="getCartLength < 1">
      You have no items in cart
      <router-link class="back" :to="{ name: 'Home' }">
        <i class="bi bi-arrow-left"></i>
        <span>Go Back</span>
      </router-link>
    </div>
    <div class="container" v-else>
      <div class="box">
        <div class="cart">
          <div class="cart-box">
            <div class="cart-box-head">
              <h3>Shopping Cart</h3>
              <h3>3 Items</h3>
            </div>
            <div class="cart-item-box">
              <div class="cart-item-title">
                <h6 class="product">Poduct details</h6>
                <h6>Quantity</h6>
                <h6>Price</h6>
                <h6>Total</h6>
              </div>
              <div class="overflow">
                <div
                  class="cart-item"
                  v-for="(item, index) in getCart"
                  :key="index"
                >
                  <div class="cart-product">
                    <div class="img-box">
                      <img :src="item.image" alt="" />
                    </div>

                    <div class="cart-details">
                      <h5>{{ item.name }}</h5>
                      <h6>{{ item.brand }}</h6>
                      <button
                        @click="removeItem(item.id)"
                        type="button"
                        class="btn-remove"
                      >
                        Remove
                      </button>
                    </div>
                  </div>
                  <div class="cart-quantity">
                    <button class="btn-quan" @click="decrease(item.id)">
                      <i class="bi bi-dash"></i>
                    </button>
                    <input
                      type="text"
                      :ref="'input-' + item.id"
                      :value="item.quantity"
                    />
                    <button class="btn-quan" @click="increase(item.id)">
                      <i class="bi bi-plus"></i>
                    </button>
                  </div>
                  <div class="cart-price">${{ item.price }}</div>
                  <div class="cart-price">${{ item.total.toFixed(2) }}</div>
                </div>
              </div>
              <a href="#" class="back">
                <i class="bi bi-arrow-left"></i>
                <span>Continue Shopping</span>
              </a>
            </div>
          </div>
          <div class="cart-order">
            <h3>Order Summary</h3>
            <div class="summary">
              <div class="itms">
                <h6>ITEMS {{ getCartLength }}</h6>
                <h6>$ {{ getCartTotal.toFixed(2) }}</h6>
              </div>
              <div class="input-box">
                <label for="shipping">SHIPPING</label>
                <select @change="calcTotal" name="" id="shipping">
                  <option value="standard-5">Standard Delivery - $5.00</option>
                  <option value="express-15">Express Delivery - $15.00</option>
                </select>
              </div>

              <div class="input-box">
                <label for="promo">PROMO CODE</label>
                <input type="text" id="promo" placeholder="Enter your code" />
                <button type="button" class="btn-apply">APPLY</button>
              </div>
            </div>
            <div class="total">
              <h4>TOTAL</h4>
              <h4>${{ total.toFixed(2) }}</h4>
            </div>
            <button type="button" @click="checkOut" class="checkout">
              CHECKOUT
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Loading from "./../components/Loading.vue";
import { mapGetters } from "vuex";
export default {
  components: {
    // Loading,
  },
  data() {
    return {
      total: 0,
      shipping: "standard-5",
      loading: null,
      address: "",
      modal: null,
    };
  },
  computed: {
    ...mapGetters(["getCart", "getCartLength"]),
    getCartTotal() {
      const cart = this.getCart;
      return cart.reduce((acc, curVal) => {
        const itemsTotal = (acc += curVal.price * curVal.quantity);
        this.total = itemsTotal + 5;
        return itemsTotal;
      }, 0);
    },
  },
  methods: {
    decrease(id) {
      const cart = this.getCart;
      const cartItem = cart.filter((e) => e.id === id);
      if (cartItem[0].quantity == 1) {
        return;
      }
      cartItem[0].quantity -= 1;
      const input = "input-" + cartItem[0].id;
      cartItem[0].total = parseFloat(cartItem[0].price) * cartItem[0].quantity;
      this.$refs[input].value = cartItem[0].quantity;
    },
    increase(id) {
      const cart = this.getCart;
      const cartItem = cart.filter((e) => e.id === id);
      if (cartItem[0].quantity === 5) {
        return;
      }
      cartItem[0].quantity += 1;
      const input = "input-" + cartItem[0].id;
      cartItem[0].total = parseFloat(cartItem[0].price) * cartItem[0].quantity;
      this.$refs[input].value = cartItem[0].quantity;
    },
    removeItem(id) {
      this.$store.commit("removeCartItem", id);
    },
    calcTotal(e) {
      const shipPrice = e.target.value.split("-")[1];
      this.total = this.getCartTotal + parseFloat(shipPrice);
    },
    checkOut() {
      this.loading = true;
      const payload = {
        total: this.total,
        shipping: this.shipping,
        address: this.address,
      };
      this.$store.dispatch("checkOut", payload);
      this.loading = false;
      this.total = 0;
      this.$router.push({ name: "Home" });
    },
  },
};
</script>

<style lang="scss" scoped>
.no-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 500;
  width: 100%;
  height: calc(100vh - 250px);

  a {
    margin-top: 30px;
    color: #000;
    text-decoration: none;
    font-weight: 500;

    span {
      margin-left: 7px;
    }
  }
}
</style>
