<template>
  <div class="filter-table">
    <div class="filter-group">
      <label for="category">Select Category</label>
      <select @change="setFilter" id="category">
        <option value="all">All</option>
        <option value="men">Men</option>
        <option value="women">Women</option>
        <option value="shoes">Shoes</option>
      </select>
    </div>
    <div class="filter-group">
      <label for="color">Filter Price</label>
      <input
        type="number"
        @blur="setFilter"
        id="price"
        name="price"
        placeholder="Price"
      />
    </div>
  </div>
</template>

<script>
export default {
  emits: ["filterCat"],
  name: "ShopFilter",
  data() {
    return {
      filters: {
        category: "all",
        price: null,
      },
    };
  },
  methods: {
    setFilter(e) {
      const Id = e.target.id;
      const value = e.target.value;
      const updatedFilters = {
        ...this.filters,
        [Id]: value,
      };
      this.filters = updatedFilters;
      this.$emit("filterCat", updatedFilters);
    },
  },
};
</script>
