import axios from "axios";
import axois from "axios";
const URL = "http://127.0.0.1:8000/api/v1/";

const state = {
  products: [],
  cart: JSON.parse(localStorage.getItem("cart")) || [],
  orders: [],
  productDetails: null,
};

const getters = {
  getProducts: (state) => state.products,
  getCart: (state) => state.cart,
  getCartLength: (state) => state.cart.length,
  getOrders: (state) => state.orders,
  getProduct: (state) => state.productDetails,
};

const mutations = {
  setProducts: (state, payload) => {
    state.products = payload;
  },
  setProduct: (state, payload) => {
    state.productDetails = payload;
  },
  setOrders: (state, payload) => {
    state.orders = payload;
  },
  setCart: (state, payload) => {
    if (state.cart.some((e) => e.id === payload.id)) {
      return;
    }
    state.cart.push(payload);
    localStorage.setItem("cart", JSON.stringify(state.cart));
  },
  removeCartItem: (state, payload) => {
    const cart = state.cart.filter((e) => e.id !== payload);
    state.cart = cart;
    localStorage.setItem("cart", JSON.stringify(state.cart));
  },
  deleteCart: (state) => {
    state.cart = [];
    localStorage.removeItem("cart");
  },
};

const actions = {
  fetchProducts: async ({ commit }) => {
    const response = await axois.get(`${URL}products/get`);
    commit("setProducts", response.data.data);
  },
  fetchProduct: async ({ commit }, payload) => {
    const response = await axois.get(`${URL}products/get/${payload}`);
    commit("setProduct", response.data);
  },
  fetchOrders: async (context) => {
    let config = {
      method: "get",
      url: `${URL}orders/me/`,
      headers: {
        Authorization: `Token ${context.rootState.auth.token}`,
      },
    };
    const response = await axois(config);
    context.commit("setOrders", response.data.data);
  },
  checkOut: async (context, payload) => {
    let data = JSON.stringify({
      total: payload.total,
      shipping: payload.shipping,
      cart: context.state.cart,
      address: payload.address,
    });
    let config = {
      method: "post",
      url: `${URL}orders/`,
      headers: {
        Authorization: `Token ${context.rootState.auth.token}`,
        "Content-Type": "application/json",
      },
      data: data,
    };
    axios(config);
    context.commit("deleteCart");
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
