<template>
  <nav class="navigation">
    <div class="container">
      <div class="nav-row">
        <button class="drawer" type="button">
          <i class="bi bi-list"></i>
        </button>
        <div class="logo-box">
          <router-link :to="{ name: 'Home' }">RazzyShop</router-link>
        </div>
        <ul class="nav-links">
          <li class="nav-item">
            <router-link :to="{ name: 'Home' }" class="nav-link"
              >Home</router-link
            >
          </li>
          <li class="nav-item">
            <router-link :to="{ name: 'Shop' }" class="nav-link"
              >Shop</router-link
            >
          </li>
          <li class="nav-item">
            <router-link
              :to="{ name: 'Order' }"
              v-if="isLoggedIn"
              class="nav-link"
              >Tracking</router-link
            >
          </li>
          <li class="nav-item" v-if="!isLoggedIn">
            <router-link :to="{ name: 'Login' }" class="nav-link"
              >Login</router-link
            >
          </li>
          <li class="nav-item" v-if="!isLoggedIn">
            <router-link :to="{ name: 'Register' }" class="nav-link"
              >Register</router-link
            >
          </li>
        </ul>
        <div class="nav-actions">
          <router-link :to="{ name: 'Cart' }" class="cart">
            <i class="bi bi-cart3"></i>
            <span>{{ getCartLength }}</span>
          </router-link>
          <button type="button">
            <i class="bi bi-search"></i>
          </button>
          <router-link :to="{ name: 'Profile' }" v-if="isLoggedIn">
            <i class="bi bi-person-circle"></i>
          </router-link>
          <button @click="logout" type="button" v-if="isLoggedIn">
            <i class="bi bi-arrow-right-square"></i>
          </button>
        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["isLoggedIn", "getCartLength"]),
    getInitail() {
      const user = this.$store.getters.getUser;
      const firstname = user.first_name;
      const lastname = user.last_name;

      const initial = firstname.split("")[0] + lastname.split("")[0];
      return initial;
    },
  },
  methods: {
    logout() {
      this.$store.dispatch("logout");
      this.$router.push({ name: "Home" });
    },
  },
};
</script>

<style lang="scss"></style>
