<template>
  <section class="shop">
    <div class="container">
      <div class="shop-box">
        <shop-filter @filterCat="filterByCategory"></shop-filter>
        <div class="shop-container">
          <h4 class="shop-head">Shop({{ filteredProducts.length }})</h4>
          <div class="shop-grid">
            <shop-item
              v-for="product in filteredProducts"
              :key="product.id"
              :id="product.id"
              :slug="product.slug"
              :name="product.name"
              :price="product.price"
              :image="product.get_thumbnail"
              :category="product.category"
            ></shop-item>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import ShopItem from "@/components/shop/ShopItem.vue";
import ShopFilter from "@/components/shop/ShopFilter.vue";
export default {
  components: {
    ShopItem,
    ShopFilter,
  },
  data() {
    return {
      loading: null,
      activefilters: {
        category: "all",
        price: "",
      },
    };
  },
  computed: {
    filteredProducts() {
      const products = this.$store.getters.getProducts;
      return products.filter((product) => {
        if (
          this.activefilters.category === "all" ||
          parseFloat(product.price) <= parseInt(this.activefilters.price)
        ) {
          return true;
        }
        if (
          this.activefilters.category &&
          product.category === this.activefilters.category
        ) {
          return true;
        }

        if (
          this.activefilters.price &&
          parseFloat(product.price) <= parseInt(this.activefilters.price)
        ) {
          return true;
        }
        return false;
      });
    },
  },
  methods: {
    filterByCategory(updatedFilters) {
      this.activefilters = updatedFilters;
    },
  },
  created() {
    this.loading = true;
    this.$store.dispatch("fetchProducts");
    this.loading = false;
  },
};
</script>

