<template>
  <div class="details">
    <Alert v-if="alert" :msg="msg" :close="close" />
    <div class="modal" v-if="error">
      <div class="modal-box">
        <div class="modal-title error">
          <h3>Error</h3>
        </div>
        <div class="modal-body">
          <p>{{ errMsg }}</p>
        </div>
        <div class="modal-footer">
          <button @click="closeModal" class="btn-cancel btn btn-round">
            Close
          </button>
        </div>
      </div>
    </div>
    <div class="container">
      <div class="details-box">
        <div class="detail-image">
          <div class="figure-image">
            <img :src="detail.data.images[0].get_image" alt="" />
          </div>
          <div class="image-select">
            <div
              class="image-select-box"
              v-for="(image, index) in detail.data.images"
              :key="index"
            >
              <img :src="image.get_image" alt="" />
            </div>
          </div>
        </div>
        <div class="detail-info">
          <h4 class="detail-title">{{ detail.data.name }}</h4>
          <h4 class="detail-price">${{ detail.data.price }}</h4>

          <div class="brands">
            <span class="cat"
              >Category: <span>{{ detail.data.category }}</span></span
            >
            <span class="cat"
              >Availabily:
              <span>{{ checkAval(detail.data.available) }}</span></span
            >
            <span class="cat"
              >Tags: <span>{{ detail.data.tags }}</span></span
            >
          </div>

          <div class="summary">
            <p>
              {{ detail.data.summary }}
            </p>
          </div>
          <div class="quantity-box">
            <button type="button" @click="decrease">
              <i class="bi bi-dash"></i>
            </button>
            <input type="text" v-model.number="quantity" />
            <button type="button">
              <i class="bi bi-plus" @click="increase"></i>
            </button>
          </div>
          <div class="add-box">
            <button @click="addToCart" class="btn btn-primary btn-round">
              ADD TO CART
            </button>
          </div>
        </div>
      </div>
      <div class="tabs">
        <button
          type="button"
          @click="changeMode('des')"
          class="tab"
          :class="{ active: mode === 'des' }"
        >
          Description
        </button>
      </div>
      <div class="des" v-if="mode === 'des'">
        <p>
          {{ detail.data.description }}
        </p>
      </div>
    </div>
  </div>
</template>

<script>
// import ReviewItem from "../components/shop/ReviewItem.vue";
import Alert from "./../components/Alert.vue";

export default {
  components: {
    // ReviewItem,
    Alert,
  },
  data() {
    return {
      loading: null,
      mode: "des",
      quantity: "1",
      msg: "Added to cart!!",
      alert: null,
      error: null,
      errMsg: "",
      stars: {
        val: 1,
        isValid: true,
      },
      review: {
        val: "",
        isValid: true,
      },
    };
  },
  computed: {
    detail() {
      return this.$store.getters.getProduct;
    },
  },
  methods: {
    increase() {
      if (this.quantity === 5) {
        return;
      }
      this.quantity++;
    },
    decrease() {
      if (this.quantity === 1) {
        return;
      }
      this.quantity--;
    },
    checkAval(e) {
      if (e) {
        return "In Stock";
      }
      return "Out of stock";
    },
    changeMode(mode) {
      this.mode = mode;
    },
    addToCart() {
      const cartItem = {
        id: this.detail.data.id,
        name: this.detail.data.name,
        price: this.detail.data.price,
        image: this.detail.data.get_thumbnail,
        brand: this.detail.data.category,
        quantity: 1 * parseInt(this.quantity),
        total: parseFloat(this.detail.data.price) * parseFloat(this.quantity),
      };
      this.$store.commit("setCart", cartItem);
      this.alert = true;
      setTimeout(function () {
        this.alert = null;
      }, 2000);
    },
    close() {
      this.alert = null;
    },
  },
  created() {
    this.$store.dispatch("fetchProduct", this.$route.params.slug);
  },
};
</script>

<style lang="scss" scoped>
.no-reviews {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.star-input {
  width: 20%;
  border: 2px solid #000;
}
</style>
