import axios from "axios";
const URL = "http://127.0.0.1:8000/api/v1/";

const state = {
  token: localStorage.getItem("auth_token"),
  user: localStorage.getItem("user"),
};

const getters = {
  isLoggedIn: (state) => !!state.token,
  getUser: (state) => state.user,
  getToken: (state) => state.token,
};

const mutations = {
  setToken: (state, payload) => {
    state.token = payload;
    localStorage.setItem("auth_token", payload);
  },
  setUser: (state, payload) => {
    state.user = payload;
    localStorage.setItem("user", JSON.stringify(payload));
  },
};

const actions = {
  login: async ({ commit, dispatch }, payload) => {
    let config = {
      method: "post",
      url: `${URL}auth/token/login/`,
      headers: {
        "Content-Type": "application/json",
      },
      data: payload,
    };
    const response = await axios(config);
    commit("setToken", response.data.auth_token);
    dispatch("getUser", response.data.auth_token);
  },

  getUser: async ({ commit }, payload) => {
    let config = {
      method: "get",
      url: `${URL}auth/users/me/`,
      headers: {
        Authorization: `Token ${payload}`,
      },
    };
    const response = await axios(config);
    commit("setUser", response.data);
  },
  updateUser: async ({ commit, state }, payload) => {
    let data = JSON.stringify({
      first_name: payload.first_name,
      last_name: payload.last_name,
      phone_number: payload.phone_number,
    });
    let config = {
      method: "put",
      url: `${URL}auth/users/me/`,
      headers: {
        Authorization: `Token ${state.token}`,
        "Content-Type": "application/json",
      },
      data: data,
    };
    try {
      const response = await axios(config);
      console.log(response.data);
      commit("setUser", response.data);
    } catch (err) {
      console.log(err.response.data);
    }
  },
  logout: ({ commit }) => {
    commit("setToken", null);
    commit("setUser", null);
    localStorage.removeItem("auth_token");
    localStorage.removeItem("user");
  },
  changePassword: async (context, payload) => {
    let data = JSON.stringify({
      new_password: payload.newPassword,
      re_new_password: payload.confirmPassword,
      current_password: payload.currentPassword,
    });

    let config = {
      method: "post",
      url: `${URL}auth/users/set_password/`,
      headers: {
        Authorization: `Token ${context.state.token}`,
        "Content-Type": "application/json",
      },
      data: data,
    };

    try {
      await axios(config);
      context.commit("setToken", null);
      context.commit("setUser", null);
    } catch (err) {
      console.log(err);
    }
  },
};

export default {
  state,
  getters,
  mutations,
  actions,
};
