<template>
  <div class="container">
    <Alert v-if="alert" :msg="msg" :close="close" />
    <div class="profile">
      <div class="sidebar">
        <button
          :class="{ active: mode === 'profile' }"
          @click="setMode('profile')"
        >
          <i class="bi bi-person-circle"></i>
          <span>Profile</span>
        </button>
        <button
          :class="{ active: mode === 'password' }"
          @click="setMode('password')"
        >
          <i class="bi bi-key"></i>
          <span>Change Password</span>
        </button>
      </div>
      <div class="content">
        <div class="content-profile" v-if="mode === 'profile'">
          <h3>Profile</h3>
          <form @submit.prevent="updateUser">
            <div class="form-group">
              <input type="text" ref="firstname" :value="getuser.first_name" />
            </div>
            <div class="form-group">
              <input type="text" ref="lastname" :value="getuser.last_name" />
            </div>
            <div class="form-group">
              <input type="email" ref="email" :value="getuser.email" />
            </div>
            <div class="form-group">
              <input type="tel" ref="number" :value="getuser.phone_number" />
            </div>
            <div class="form-group">
              <button type="submit" class="btn btn-primary btn-round">
                Save Changes
              </button>
            </div>
          </form>
        </div>
        <div class="content-password" v-if="mode === 'password'">
          <h3>Change Password</h3>
          <form @submit.prevent="changePassword">
            <div class="form-group">
              <input
                type="password"
                v-model="currentPassword"
                placeholder="Current Password"
              />
            </div>
            <div class="form-group">
              <input
                type="password"
                v-model="newPassword"
                placeholder="New Password"
              />
            </div>
            <div class="form-group">
              <input
                type="password"
                v-model="confirmPassword"
                placeholder="Repeat Password"
              />
            </div>
            <div class="form-group">
              <button type="submit" class="btn btn-primary btn-round">
                Change Password
              </button>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Alert from "./../components/Alert.vue";
export default {
  components: {
    Alert,
  },
  data() {
    return {
      mode: "profile",
      alert: null,
      msg: "",
      newPassword: "",
      confirmPassword: "",
      currentPassword: "",
    };
  },
  computed: {
    getuser() {
      return this.$store.getters.getUser;
    },
  },

  methods: {
    setMode(value) {
      this.mode = value;
    },
    close() {
      this.alert = null;
    },
    updateUser() {
      const data = {
        first_name: this.$refs.firstname.value,
        last_name: this.$refs.lastname.value,
        phone_number: this.$refs.number.value,
      };
      console.log(this.$refs.firstname.value);

      this.$store.dispatch("updateUser", data);
      this.alert = true;
      this.msg = "Profile Updated";
    },
    changePassword() {
      if (this.newPassword === "" || this.newPassword.length < 8) {
        (this.alert = true),
          (this.msg = "Password must be at leaat 8 characters");
        return;
      }
      if (
        this.confirmPassword === "" ||
        this.confirmPassword !== this.newPassword
      ) {
        (this.alert = true), (this.msg = "Passwords do not match");
        return;
      }
      if (this.currentPassword === "") {
        (this.alert = true), (this.msg = "Invalid Password");
        return;
      }
      const data = {
        newPassword: this.newPassword,
        confirmPassword: this.confirmPassword,
        currentPassword: this.currentPassword,
      };
      this.$store.dispatch("changePassword", data);
      this.$router.replace({ name: "Login" });
    },
  },
  created() {
    this.$store.dispatch("getUser", this.$store.getters.getToken);
  },
};
</script>
