<template>
  <div class="auth">
    <div class="modal" v-if="error">
      <div class="modal-box">
        <div class="modal-title error">
          <h3>Error</h3>
        </div>
        <div class="modal-body">
          <p>{{ errMsg }}</p>
        </div>
        <div class="modal-footer">
          <button @click="closeModal" class="btn-cancel btn btn-round">
            Close
          </button>
        </div>
      </div>
    </div>
    <div class="auth-box">
      <h3>Register</h3>
      <form @submit.prevent="onSubmit">
        <div class="form-group">
          <input
            type="text"
            placeholder="Firstname"
            v-model.trim="firstName.val"
            @blur="clearValidity('firstName')"
          />
        </div>
        <span class="error" v-if="!firstName.isValid"
          >Firstname can't be empty</span
        >
        <div class="form-group">
          <input
            type="text"
            placeholder="Lastname"
            v-model.trim="lastName.val"
            @blur="clearValidity('lastName')"
          />
        </div>
        <span class="error" v-if="!lastName.isValid"
          >Firstname can't be empty</span
        >
        <div class="form-group">
          <input
            type="email"
            placeholder="Email Address"
            v-model.trim="email.val"
            @blur="clearValidity('email')"
          />
        </div>
        <span class="error" v-if="!email.isValid">Invalid email address</span>
        <div class="form-group">
          <input
            type="tel"
            placeholder="Phone Number"
            v-model.trim="phoneNumber.val"
            @blur="clearValidity('phoneNumber')"
          />
        </div>
        <span class="error" v-if="!phoneNumber.isValid"
          >Invalid phone number format</span
        >
        <div class="form-group">
          <input
            type="password"
            placeholder="Password"
            v-model="password.val"
            @blur="clearValidity('password')"
          />
        </div>
        <span class="error" v-if="!password.isValid"
          >Password must be at leaat 8 characters</span
        >
        <div class="form-group">
          <input
            type="password"
            placeholder="Confirm Password"
            v-model="passwordConfirm.val"
            @blur="clearValidity('passwordConfirm')"
          />
        </div>
        <span class="error" v-if="!passwordConfirm.isValid"
          >Passwords do no match</span
        >
        <!-- <a href="#">Forgot Password</a> -->
        <button type="submit" class="btn btn-primary">Register</button>
        <p>
          Already have an account?
          <router-link :to="{ name: 'Login' }">Login</router-link>
        </p>
        <p class="error" v-if="!formIsValid">Please fix the above errors</p>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
// import Alert from "./../components/Alert.vue";
const URL = "http://127.0.0.1:8000/api/v1/";
export default {
  components: {
    // Alert,
  },
  data() {
    return {
      firstName: {
        val: "",
        isValid: true,
      },
      lastName: {
        val: "",
        isValid: true,
      },
      email: {
        val: "",
        isValid: true,
      },
      phoneNumber: {
        val: "",
        isValid: true,
      },
      password: {
        val: "",
        isValid: true,
      },
      passwordConfirm: {
        val: "",
        isValid: true,
      },
      errMsg: "",
      formIsValid: true,
      error: null,
    };
  },
  methods: {
    clearValidity(input) {
      this[input].isValid = true;
    },
    validateForm() {
      this.formIsValid = true;
      if (this.firstName.val === "") {
        this.firstName.isValid = false;
        this.formIsValid = false;
      }
      if (this.lastName.val === "") {
        this.lastName.isValid = false;
        this.formIsValid = false;
      }
      if (this.email.val === "" || !this.email.val.includes("@")) {
        this.email.isValid = false;
        this.formIsValid = false;
      }
      if (this.phoneNumber.val === "") {
        this.phoneNumber.isValid = false;
        this.formIsValid = false;
      }
      if (this.password.val === "" && this.password.length < 8) {
        this.password.isValid = false;
        this.formIsValid = false;
      }
      if (this.password.val !== this.passwordConfirm.val) {
        this.passwordConfirm.isValid = false;
        this.formIsValid = false;
      }
    },
    async onSubmit() {
      this.validateForm();

      if (!this.formIsValid) {
        return;
      }
      const userInfo = JSON.stringify({
        first_name: this.firstName.val,
        last_name: this.lastName.val,
        email: this.email.val,
        phone_number: this.phoneNumber.val,
        password: this.password.val,
      });
      let config = {
        method: "post",
        url: `${URL}auth/users/`,
        headers: {
          "Content-Type": "application/json",
        },
        data: userInfo,
      };
      try {
        const response = await axios(config);
        this.$store.commit("setUser", response.data);
        this.$router.push({ name: "Login" });
      } catch (error) {
        if (Object.keys(error.response.data).includes("email")) {
          this.errMsg = error.response.data.email[0];
        } else if (Object.keys(error.response.data).includes("phone_number")) {
          this.errMsg = error.response.data.phone_number[0];
        } else {
          this.errMsg = "Something went wrong!!";
        }
        this.error = true;
      }
    },
    closeModal() {
      this.error = false;
      this.errMsg = "";
    },
  },
};
</script>

<style lang="scss" scoped>
.error {
  color: orangered;
}
</style>
