<template>
  <div class="order-box">
    <div class="no-cart" v-if="getOrders.length < 1">
      You haven't order anything yet!!
      <router-link class="back" :to="{ name: 'Home' }">
        <i class="bi bi-arrow-left"></i>
        <span>Go Back</span>
      </router-link>
    </div>
    <div v-else class="order">
      <h3>My Orders</h3>
      <div class="order-list">
        <div class="header">
          <div>Number</div>
          <div>Order</div>
          <div>Price</div>
          <div>Date</div>
          <div>Status</div>
        </div>
        <div class="content">
          <div class="item" v-for="(order, index) in getOrders" :key="order.id">
            <div># {{ index + 1 }}</div>
            <div>{{ order.items.length }} Items</div>
            <div>$ {{ order.total.toFixed(2) }}</div>
            <div>{{ formatDate(order.date_added) }}</div>
            <div>{{ formatCompleted(order.completed) }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      dateOptions: { year: "numeric", month: "short", day: "numeric" },
    };
  },
  computed: {
    ...mapGetters(["getOrders"]),
  },
  created() {
    this.$store.dispatch("fetchOrders");
  },
  methods: {
    formatCompleted(value) {
      if (value == true) {
        value = "Done";
      } else {
        value = "In progress";
      }
      return value;
    },
    formatDate(date) {
      const newDate = new Date(date).toLocaleDateString(
        "en-us",
        this.dateOptions
      );
      return newDate;
    },
  },
};
</script>
<style lang="scss" scoped>
.order-box {
  width: 100%;
  height: calc(100vh - 120px);
  display: flex;
  justify-content: center;
  align-items: center;
  .no-cart {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 20px;
    font-weight: 500;

    a {
      margin-top: 30px;
      color: #000;
      text-decoration: none;
      font-weight: 500;

      span {
        margin-left: 7px;
      }
    }
  }

  .order {
    width: 70%;
    h3 {
      font-size: 25px;
      margin-bottom: 30px;
    }
    &-list {
      width: 100%;
      .header {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        div {
          font-size: 18px;
          font-weight: 500;
          margin-bottom: 20px;
          flex-basis: 20%;

          &:not(:first-child) {
            text-align: center;
          }
        }
      }

      .content {
        width: 100%;
        border-top: 2px solid #ccc;

        .item {
          width: 100%;
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding: 15px 0;
          border-bottom: 2px solid #eee;
          transition: all 0.3s;
          cursor: pointer;

          &:hover {
            background-color: #f7f7f7;
          }

          div {
            font-size: 16px;
            font-weight: 500;
            width: 30%;

            &:not(:first-child) {
              text-align: center;
            }
          }
        }
      }
    }
  }
}
</style>
