<template>
  <div class="shop-item" :id="id">
    <div class="shop-item-img">
      <img :src="image" alt="" />
    </div>
    <h4 class="shop-title">{{ name }}</h4>
    <h4 class="shop-price">${{ price }}</h4>
    <div class="icon-overlay">
      <router-link
        :to="{ name: 'ProductDetail', params: { slug: slug } }"
        class="like"
      >
        <i class="bi bi-arrow-right"></i>
      </router-link>
      <button class="cart" @click="addToCartShop">
        <i class="bi bi-cart3"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ShopItem",
  props: ["id", "slug", "name", "price", "image", "category"],
  data() {
    return {
      quantity: 1,
    };
  },
  methods: {
    addToCartShop() {
      const cartItem = {
        id: this.id,
        name: this.name,
        price: this.price,
        image: this.image,
        brand: this.category,
        total: parseFloat(this.price) * parseFloat(this.quantity),
        quantity: this.quantity,
      };
      this.$store.commit("setCart", cartItem);
    },
  },
};
</script>
